.page-404 {
    width: 100%;
    height: 100vh;
    background-image: url('../../img/intro-bg.png');
    background-repeat: no-repeat;
    background-size: cover;

    @include mobile-small {
        background-image: url('../../img/404-Mobile-bg.jpg');
        width: 100%;
        height: 100vh;
        object-fit: cover;
        display: block;
    }

    &__wrapper {
        position: absolute;
        top: 50%;
        right: 160px;
        width: 388px;
        text-align: center;
        transform: translateY(-50%);
        @include for-desktop-large {
            right: 50px;
        }
        @include for-desktop{
            width: 240px;
            right: 70px;
        }
        @include mobile-small {
            position: static;
            width: 225px;
            margin-top: -150px;
        }
    }

    &__container {
        display: flex;
        max-width: 1440px;
        margin: 0 auto;
        justify-content: center;
        align-items: center;
        padding: 40px 30px 0 30px;
        position: relative;

        @include for-desktop{
            padding: 32px;
        }
        @include mobile-small {
            flex-direction: column;
            padding: 16px;
        }
    }

    &__text {
        font-family: var(--second-family);
        font-weight: 500;
        font-size: 120px;
        line-height: 1;
        text-align: center;
        color: var(--color---white);

        @include for-desktop-large {
            font-size: 80px;
        }

        @include for-desktop {
            font-size: 40px;
        }

        @include mobile-small {
            font-size: 60px;
        }
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 500;
        font-size: 40px;
        line-height: 150%;
        color: #b3ffc7;

        @include for-desktop-large {
            font-size: 30px;
        }

        @include for-desktop {
            font-size: 20px;
        }

    }

    &__img {
        
        @include mobile-small {
            width: 100%;
            max-width: 310px;
            height: auto;
            display: block;
            background-size: cover;
        }
    }

    &__btn {
        margin-top: 30px;
        width: 346px;
        padding-left: 46.5px;
        padding-right: 77px;

        @include for-desktop {
            font-size: 20px;
            margin-top: 15px;
            height: 56px;
            width: 240px;
            margin: 0 auto;
            padding-left: 46.5px;
            padding-right: 24.5px;
        }
        @include pre-mobile {
            height: auto;
            font-size: 14px;
            width: 210px;
            padding-top: 10px;
            padding-bottom: 10px;
        }
        @include mobile-small {
            width: 100%;
            margin-top: 20px;
        }
    }
    &__btn-effect {
        @include for-desktop {
            width: 100%;
        }
    }

    &__btn-text {
        font-weight: 500;
        font-size: 24px;
        line-height: 150%;
        color: var(--tekst-zagolovok);

        @include for-desktop {
            font-size: 20px;
        }
        @include pre-mobile {
            line-height: 1;
            font-size: 16px;
        }
    }
    
    &__arrow {
        margin-left: 84px;

        @include for-desktop {
            margin-left: 0;
        }
    }

}