.prices {
  padding-bottom: 120px;

  @include tablet {
    padding-bottom: 80px;
	}

  @include mobile-final {
    padding-bottom: 64px;
  }

  &__title {
    display: inline-block;
    margin-bottom: 32px;
    width: 100%;
    max-width: 343px;

    @include tablet {
      font-size: 32px;
	  }

    @include mobile-final {
      max-width: 220px;
      font-size: 20px;
    }
  }
  
  &__list {
    display: flex;
    gap: 32px;

    @include tablet {
      gap: 18px;
	  }

    @include pre-mobile {
      flex-direction: column;
	  }
  }
}

.price-card {
  border-radius: 15px;
  width: 378px;
  height: 846px;

  @include for-desktop-large {
    margin: 0 auto;
    width: 360px;
	}

  @include tablet {
    width: 324px;
    height: 627px;
	}

  @include pre-mobile {
    width: 100%;
    height: 575px;
    max-width: 334px;
  }

  @include mobile-final {
    height: auto;
  }

  &__afrticle {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 32px;
    height: 100%;

    @include for-desktop-huge {
      padding: 24px;
	  }

    @include for-desktop {
      padding: 18px;
	  }

    @include tablet {
      padding: 16px;
	  }

    @include small-tablet {
      padding: 15px;
	  }
  }

  &__wrapper {
    position: relative;
  }

  &__badge {
    position: absolute;
    left: 0;
    top: 0;
    padding: 2px 15px;
    font-weight: 400;
    font-size: 14px;
    border-radius: 2px;
    line-height: 125%;
    background: var(--color---white);
    letter-spacing: 0.1px;
    color: var(--color---black);

    @include mobile-final {
      left: auto;
      right: 0;
	  }
  }

  &__header {
    margin-top: 42px;

    @include tablet {
      margin-top: 32px;
	  }

    @include mobile-final {
      margin-top: 0;
      font-size: 18px;
	  }
  }

  &__title {
    margin-bottom: 23px;
    font-weight: 700;
    font-size: 36px;
    letter-spacing: -2px;
    color: var(--osnovnoy-emerald);

    @include for-desktop-large {
      font-size: 32px;
	  }

    @include for-desktop {
      font-size: 28px;
	  }

    @include tablet {
      margin-bottom: 10px;
      font-size: 18px;
      letter-spacing: -1px;
	  }

    @include pre-mobile {
      font-size: 20px;
	  }

    @include mobile-final {
      font-size: 18px;
      letter-spacing: -0.6px;
    }
  }

  &__subtitle {
    margin-bottom: 10px;
    width: 259px;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.2px;
    color: var(--tekst-abzac);

    @include for-desktop {
      font-size: 18px;
	  }

    @include tablet {
      width: 100%;
      font-size: 16px;
	  }
  }

  &__discount {
    display: inline-flex;
    margin-bottom: 9px;
    border-radius: 5px;
    padding: 5px 13px;
    width: 120px;
    height: 28px;
    font-weight: 400;
    font-size: 14px;
    background: var(--akcentnyy-rose);
    line-height: 125%;
    color: var(--tekst-zagolovok);

    @include small-tablet {
      margin-bottom: 8px;
      width: auto;
      padding: 5px 18px;
      height: 24px;
      font-size: 12px;
      letter-spacing: -1px;
	  }
  }

  &__price {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    border-bottom: 1px solid var(--tekst-abzac);
    padding-bottom: 19px;
    gap: 10px;
    
    @include tablet {
      padding-bottom: 16px;
	  }
  }

  &__price-act {
    display: inline-block;
    font-weight: 700;
    font-size: 36px;
    line-height: 1;
    letter-spacing: -1px;
    color: var(--tekst-zagolovok);

    @include for-desktop {
      font-size: 30px;
	  }

    @include tablet {
      font-size: 20px;
	  }
  }

  &__price-old {
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: -1px;
    text-decoration: line-through;
    color: var(--tekst-abzac);

    @include for-desktop {
      font-size: 16px;
	  }

    @include tablet {
      font-size: 14px;
	  }
  }

  &__advantages {
    display: flex;
    flex-direction: column;
    gap: 10px;

    @include tablet {
      gap: 4px;
	  }

    @include mobile-final {
      gap: 10px;
    }
  }

  &__advantages-title {
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    color: var(--tekst-zagolovok);

    @include tablet {
      font-size: 14px;
	  }

    @include pre-mobile {
      font-size: 16px;
	  }

    @include mobile-final {
      font-size: 14px;
      line-height: 125%;
      letter-spacing: 0.03em;
    }
  }

  &__advantages-desc {
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.1px;
    color: var(--tekst-abzac);

    @include tablet {
      font-size: 13px;
	  }

    @include pre-mobile {
      font-size: 16px;
	  }

    @include mobile-final {
      font-size: 14px;
      line-height: 125%;
      letter-spacing: 0.03em;
    }
  }

  &__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--tekst-zagolovok);
    border-radius: 10px;
    padding: 16px 32px;
    font-weight: 500;
    font-size: 30px;
    line-height: 150%;
    letter-spacing: -1px;
    text-align: center;
    color: var(--tekst-zagolovok);
    transition: all 0.3s ease-in-out;

    @include for-desktop-large {
      font-size: 26px;
	  }

    @include tablet {
      padding: 14px 12px;
      font-size: 20px;
	  }

    @include mobile-final {
      margin-top: 10px;
      padding: 8px 14px;
      font-size: 16px;
    }

    @include hover {
      color: var(--osnovnoy-emerald);
      background: var(--tekst-zagolovok);
    }
  }
}

.selfMade-card {
  border: 1px solid var(--osnovnoy-emerald);
  background: linear-gradient(
    148deg,
    rgba(16, 185, 129, 0.12) 0%,
    rgba(7, 83, 58, 0.12) 100%
  );
}

.proSupport-card {
  border: 1px solid var(--dopolnitelnyy-blue);
  background: linear-gradient(
    136deg,
    rgba(96, 165, 250, 0.5) 0%,
    rgba(58, 112, 178, 0.5) 100%
  );
}

.proSupport-card-title {
  color: var(--tekst-zagolovok);
}

.carrerJump-card {
  border: 1px solid var(--color---red);
  background: rgba(255, 99, 112, 0.12);
}

.carrerJump-card-title {
  color: var(--color---red);
}
