.footer {
    padding: 40px 0;
    background-image: url('../../img/footer/footer-bg.png');
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 1px;
    color: var(--tekst-abzac);

    @include pre-mobile {
        font-size: 14px;
    }

    @include mobile-final {
        padding-top: 18.5px;
        font-size: 16px;
    }

    @include mobile {
        background-image: url('../../img/footer/footer-bg-mobile.png');
    }

    @include mobile-final {
        padding-bottom: 16px;
    }

    &__inner {
        display: flex;
        justify-content: space-between;
        height: 216px;
        gap: 8px;
        @include mobile {
            flex-direction: column;
            height: auto;
            gap: 10px;
        }
    }

    &__row {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        max-width: 250px;
        width: 100%;

        @include pre-mobile {
            max-width: 200px;
        }

        @include mobile {
            flex-direction: row;
            max-width: 100%;
        }

        @include mobile-final {
            flex-direction: column;
            gap: 2px;
        }
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 700;
        font-size: 32px;
        letter-spacing: -1px;
        color: var(--osnovnoy-hover);
        margin-bottom: 45px;

        @include tablet {
            font-size: 16px;
            margin-bottom: 32px;
        }
        
        @include mobile-final {
            margin-bottom: 28px;
        }
    }

    &__item {
        margin-bottom: 16px;

        @include mobile-final {
            margin-bottom: 5px;
        }

        &-name {
            font-weight: 400;
            font-size: 18px;
            line-height: 150%;
            letter-spacing: .2px;
            color: var(--tekst-zagolovok);

            @include tablet {
                font-size: 14px;
            }

            @include mobile {
                flex-direction: row;
                max-width: 100%;
                font-size: 18px;
            }

            @include mobile-final {
                flex-direction: column;
                font-size: 14px;
            }
        }

        &-contact {
            font-weight: 400;
            font-size: 18px;
            line-height: 150%;
            letter-spacing: 0.05em;
            color: var(--tekst-abzac);

            @include tablet {
                font-size: 16px;
            }
        }
    }
    
    &__item-icon {
        width: 13px;
        height: auto;
    }

    &__information {
        max-width: 231px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 16px;

        @include tablet {
            max-width: 209px;
            justify-content: flex-start;
            gap: 0px;
        }

        @include mobile {
            flex-direction: row;
            max-width: 100%;
            gap: 40px;
            align-items: center;
        }

        @include mobile-final {
            flex-direction: column;
            align-items: baseline;
            gap: 10px;
        }
    }

    &__userAgr {
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.05em;
        text-decoration: underline;
        text-decoration-skip-ink: none;
        color: var(--tekst-abzac);

        @include tablet {
            margin-bottom: 10px;
            font-size: 14px;
        }
        @include mobile-final {
            font-size: 14px;
            line-height: 125%;
            letter-spacing: 0.03em;
            margin-bottom: 0px;
        }
    }

    &__copyright {
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 1px;
        color: var(--tekst-abzac);

        @include tablet {
            font-size: 14px;
            margin-top: 16px;
        }

        @include mobile {
            width: 200px;
            margin-top: 0px;
        }
    }

}

.socials {
    display: flex;
    align-items: center;
    gap: 10px;
    @include mobile {
        width: 198px;
    }
    @include mobile-final {
        width: 100%;
        margin-top: 10px;
    }

    &__item {
        border-radius: 5px;
        width: 32px;
        height: 32px;
        display: flex;
        justify-content: center;
        align-items: center;
        @include mobile-final {
            width: 24px;
            height: 24px;
        }
    }

}