.nav {

    @include small-tablet {
        border-radius: 15px 15px 0 0;
        width: 768px;
        height: 363px;
        position: fixed;
        background: var(--fon-charcoal);
        left: 0;
        bottom: 0;
        z-index: 1;
        opacity: 0;
        transform: translateY(150%);
        transition: transform 0.4s ease, opacity 0.4s ease;
    }
    @include mobile-final {
        width: 100%;
        height: 304px;
    }

    &__list {
        display: flex;
        align-items: center;
        gap: 66px;

        @include for-desktop {
            font-size: 18px;
            gap: 20px;
        }
        @include small-tablet {
            flex-direction: column;
            align-items: flex-start;
            gap: 10px;
        }
    }

    &.active {
        @include small-tablet {
            z-index: 9;
            opacity: 1;
            transform: translate(0%);
        }
    }

    &__item {
        @include small-tablet {
            padding: 16px 20px;
            width: 100%;
            border-radius: 10px;
        }

        @include mobile-final {
            padding: 10px 16px;
        }
    }

    &__item:first-child {
        @include small-tablet {
            background: rgba(5, 150, 105, 0.12);
        }
    }

    &__link {
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.05em;
        color: var(--tekst-zagolovok);
        transition: color .3s ease-in-out;
        
        @include hover {
            color: var(--osnovnoy-hover);
        }

        @include for-desktop {
            font-size: 15px;
        }

        @include small-tablet {
            display: inline-block;
            width: 100%;
            font-family: var(--font-family);
            font-weight: 700;
            font-size: 18px;
            color: var(--color---white);
        }

        @include mobile-final {
            font-size: 16px;
        }
    }
}

.burger-menu {
    display: none;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 24px;
    height: 24px;
    gap: 3px;
    cursor: pointer;

    &__item {
        display: inline-block;
        width: 20px;
        border-radius: 5px;
        height: 2px;
        background-color: var(--tekst-zagolovok);
        transition: all .3s ease-in-out;
    }

    @include small-tablet {
        display: flex;
    }
}

.burger-menu.active .burger-menu__item:nth-child(3) {
    margin-left: 10px;
    width: 10px;
}