*,
*::before,
*::after {
  box-sizing: border-box;
}

.stop-scroll {
  overflow: hidden;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

.icon {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hidden {
  visibility: hidden;
}

a {
  text-decoration: none;

  &:focus {
    outline: 1px solid var(--color---white);
  }
}

button {
  &:focus {
    outline: 1px solid var(--color---white);
  }
}

.site-container {
  position: relative;
}

p {
  margin: 0;
  padding: 0;
  font: inherit;
  line-height: normal;
  color: inherit;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
}

.no-wrap {
  white-space: nowrap;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  content: none !important;
  display: none !important;
}

input:focus {
  border: 1px solid var(--color---white);
  outline: none;
  box-shadow: 0 0 0 2px ;
  outline: 1px solid var(--color---white);
}

input,
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 1000px transparent inset !important;
  background-color: transparent !important;
  color: #fff;
}

textarea,
textarea:hover,
textarea:focus,
textarea:active {
  -webkit-box-shadow: none !important;  
  background-color: transparent !important; 
  outline: none;     
  box-shadow: none !important;                               
  border-color: var(--color---white);             
}

.checkBox {
  position: absolute;
  left: 0;
  top: 18px;
  display: block;
  width: 12px;
  height: 12px;
  cursor: pointer;
  border-radius: 3px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 0 2px #fff;
}

.checkBox div {
  width: 30px;
  height: 30px;
  background-color: #fff;
  top: -65px;
  left: -75px;
  position: absolute;
  transform: rotateZ(45deg);
  z-index: 100;
}

.checkBox input[type=checkbox]:checked + div {
  left: -10px;
  top: -10px;
}

.checkBox input[type=checkbox] {
  position: absolute;
  left: 50px;
  visibility: hidden;
}

.transition {
  transition: 300ms ease;
}

.select__option[aria-selected="true"] {
  background: rgba(16, 185, 129, 0.12);
}

dl,
dt,
dd {
  margin: 0;
  padding: 0;
}