.reviews {
    padding-bottom: 120px;

    @include tablet {
        padding-bottom: 80px;
	}

    @include mobile-final {
        padding-bottom: 56px;
    }

    &__title {
        margin-bottom: 32px;

        @include tablet {
            margin-bottom: 75px;
            font-size: 32px;
	    }

        @include mobile-final {
            margin-bottom: 24px;
            font-size: 20px;
            letter-spacing: -1px;
        }
    }

    &__slider-wrapper {
        display: flex;
        justify-content: space-between;
    }

    &__inner {
        position: relative;
    }

    &__slider-controls {
        position: absolute;
        right: 0;
        top: 88px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 82px;
        gap: 10px;

        @include mobile {
            top: auto;
            bottom: -45px;
	    }
    }
}

.review-card {
    display: flex;
    width: 584px;
    gap: 32px;

    @include for-desktop-huge {
        justify-content: center;
        margin: 0 auto;
        width: 100%;
        max-width: 680px;
	}

    &__content {
        display: flex;
        flex-direction: column;
        border: 3px solid var(--color---light-blue-12);
        border-radius: 15px;
        padding: 32px;
        width: 276px;

        @include for-desktop-huge {
            width: 300px;
	    }

        @include mobile-small {
            position: relative;
            border: none;
            padding: 0;
            padding-bottom: 20px;
            width: 100%;
	    }

    }

    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 123px;
        gap: 13px;

        @include mobile {
            margin-bottom: 40px;
	    }

        @include mobile-small {
            border-radius: 15px;
            padding: 8px 16px;
            width: 100%;
            background: var(--color---light-blue-12);
	    }

        @include mobile-final {
            margin-bottom: 10px;
        }
    }

    &__name,
    &__age {
        font-weight: 700;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: -0.8px;
        color: var(--osnovnoy-hover);

        @include mobile-final {
            font-size: 16px;
        }
    }

    &__icon {
        overflow: hidden;
        border-radius: 5px;
        width: 64px;
        height: 64px;
        background-size: cover;
    }

    &__text {
        overflow: hidden;
        margin-bottom: 16px;
        max-height: 162px;
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.05em;
        color: var(--tekst-abzac);
        transition: max-height 0.3s ease;

        @include mobile-small {
            border-radius: 15px;
            padding: 8px 16px;
            width: 100%;
            max-height: 148px;
            background: var(--color---light-blue-12);
	    }
    }

    &__more {
        display: inline-flex;
        width: fit-content;
        font-weight: 400;
        font-size: 14px;
        line-height: 125%;
        letter-spacing: 0.03em;
        text-decoration: underline;
        color: var(--tekst-abzac);
        transition: color .3s ease-in-out;
        cursor: pointer;
        text-decoration-skip-ink: none;

        @include mobile-small {
            position: absolute;
            left: 0;
            bottom: 0;
	    }

        @include hover {
            color: var(--tekst-zagolovok);
        }
    }

    &__photo {
        display: flex;
        border-radius: 15px;

        @include mobile-small {
            display: none;
        }
    }

    &__image {
        border-radius: 15px;
        height: 447px;
    }
}

.reviews__slider-prev,
.reviews__slider-next {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--osnovnoy-emerald);
    border-radius: 10px;
    width: 36px;
    height: 36px;
    transition: background-color .3s ease-in-out;

    @include hover {
        background-color: var(--osnovnoy-hover);
    }
}
