.benefits {
    position: relative;
    padding-top: 80px;
    padding-bottom: 23px;

    @include tablet  {
        padding-top: 43px;
    }

    @include mobile  {
        margin-bottom: 80px;
    }

    @include mobile-final  {
        margin-bottom: 94px;
        padding-top: 35px;
    }

    &__title {
        width: 378px;
        font-weight: 700;
        font-size: 48px;
        line-height: 1.32;
        color: var(--tekst-zagolovok);
        margin-bottom: -185px;
        letter-spacing: -0.05em;

        @include for-desktop-large  {
            margin-bottom: 0;
            width: 100%;
            max-width: 401px;
        }

        @include tablet  {
            max-width: 268px;
            font-size: 32px;
        }

        @include mobile-final {
            max-width: 250px;
            font-size: 20px;
        }
    }

    &__slider {
        position: relative;

        @include tablet  {
            margin-top: 32px;
        }

        @include mobile-final  {
            margin-top: 20px;
        }
    }

    &__box {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: masonry; 
        gap: 32px;

        @include tablet  {
            display: flex;
            font-size: 80px;
            flex-wrap: wrap;
            justify-content: center;
            gap: 16px;
        }

        @include mobile  {
            display: block;
        }
    }

    &__item {
        width: 100%;
        background: var(--color---light-blue-12);
        max-width: 378px;
        flex: 1 1 calc(50% - 20px);
        border-radius: 15px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 32px;
        position: relative;
        
        @include tablet  {
            padding: 0;
            background: transparent;
            height: auto;
            width: 100%;
            max-width: 336px;
        }
        @include pre-mobile   {
            max-width: 500px;
        }
        @include pre-mobile {
            width: 100%;
            height: auto;
            flex: 0 0 auto;
            background: transparent;
        }
        &-wrapper {
            width: 100%;
            max-width: 226px;
            @include tablet  {
                margin-bottom: 10px;
                border-radius: 15px;
                padding: 23px 20px;
                max-width: 500px;
            }
            @include mobile {
                max-width: 100%;
            }
            @include mobile-final {
                padding: 9px 15px;
                margin-bottom: 4px;
            }
        }
        &:nth-child(1) .benefits__number {
            top: 50%;
            transform: translateY(-50%);
            font-size: 200px;
            @include for-desktop-large  {
                font-size: 150px;
                top: 44%;
            }
            @include tablet  {
                font-size: 80px;
                transform: translateY(0%);
            }
            @include pre-mobile {
                font-size: 60px;
            }
        }
        &:nth-child(2) .benefits__number {
            top: 50%;
            transform: translateY(-50%);
            font-size: 240px;
            @include for-desktop-large  {
                font-size: 180px;
            }
            @include tablet  {
                font-size: 80px;
                transform: translateY(0%);
            }
            @include pre-mobile {
                font-size: 60px;
            }
            
        }
        &:nth-child(3) .benefits__number {
            font-size: 140px;
            top: 38px;
            right: -7px;
            @include for-desktop-large  {
                font-size: 120px;
            }
            @include tablet  {
                font-size: 80px;
                right: -3px;
            }
            @include pre-mobile {
                font-size: 60px;
            }
            
        }
        &:nth-child(4) .benefits__number {
            font-size: 140px;
            top: 38px;
            right: -7px;
            @include for-desktop-large  {
                font-size: 120px;
            }
            @include tablet  {
                font-size: 80px;
                right: -3px;
            }
            @include pre-mobile {
                font-size: 60px;
            }
        }
    }

    &__item-wrapper {
       @include tablet  {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background: var(--color---light-blue-12);
        }
    }

    &__number {
        position: absolute;
        right: 22px;
        font-family: var(--font-family);
        font-weight: 700;
        color: var(--tekst-zagolovok);
        @include tablet  {
            position: static;
            top: auto;
            left: auto;
            transform: translateY(0%);
        }
        @include pre-mobile {
            font-size: 60px;
        }
    }

    &__subtitle {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 36px;
        color: var(--osnovnoy-hover);
        line-height: 1.3;
        letter-spacing: -1px;
        @include tablet  {
            font-size: 24px;
        }
        @include pre-mobile {
            font-size: 20px;
        }
        @include mobile-final {
            font-size: 18px;
        }
    }

    &__item--1 {
        position: relative;
        top: -97px;
        height: 382px;
        grid-column: 1 / 2;
        grid-row: 2 / 3;
        align-items: start;
        @include tablet  {
            height: auto;
            grid-column: auto;
            grid-row: auto; 
            top: 0;
        }
    }

    &__item--2 {
        height: 600px;
        grid-column: 2 / 3;
        grid-row: 1 / 3; 
        @include tablet  {
            height: auto;
            grid-column: auto;
            grid-row: auto; 
        }
    }

    &__item--3 {
        height: 284px;
        grid-column: 3 / 4;
        grid-row: 1 / 2;
        @include tablet  {
            height: auto;
            grid-column: auto;
            grid-row: auto; 
        }
    }

    &__item--4 {
        height: 284px;
        grid-column: 3 / 4;
        grid-row: 2 / 3;
        @include tablet  {
            height: auto;
            grid-column: auto;
            grid-row: auto; 
        }
    }

    &__text {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.2px;
        color: var(--tekst-abzac);
        @include tablet  {
            font-size: 16px;
            background: var(--color---light-blue-12);
            border-radius: 15px;
            padding: 21px;
        }
        @include pre-mobile {
            font-size: 15px;
            padding: 10px;
        }
        @include mobile {
            font-size: 16px;
        }
        @include mobile-final {
            padding: 15px;
        }
    }

}

.benefits__slider-nav-wrapper {
    position: absolute;
    bottom: -31px;
    right: 32px;
    display: flex;
    justify-content: space-between;
    width: 82px;
}

.benefits-swiper-button-prev,
.benefits-swiper-button-next {
    display: none;
    @include mobile {
        display: block;
        width: 36px;
        height: 36px;
        border: 1px solid var(--osnovnoy-emerald);
        border-radius: 10px;
        background: var(--osnovnoy-hover);
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.swiper-button-disabled {
    background-color: transparent;
}

@media (max-width: 650px) {
  .benefits-swiper {
    width: 100%;
    overflow: hidden;
  }

  .benefits__item {
    flex: 0 0 100% !important; /*    100%   */
    max-width: 100% !important;
    width: 100% !important;
    box-sizing: border-box !important;
    border-radius: 15px;
    padding: 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .benefits__box.swiper-wrapper {
    display: flex !important;
    transition-property: transform !important;
  }

  .benefits__item.swiper-slide {
    flex-shrink: 0 !important;
    width: 100% !important;
  }
}
