.contact-form {
    padding-bottom: 120px;

    @include tablet {
        padding-bottom: 80px;
        margin-top: 54px;
    }

    @include mobile-final {
        padding-bottom: 65px;
    }

    &__wrapper {
        background-color: rgba(16, 185, 129, 0.7);
        background-image: url('../../img/form/form-bg.png');
        background-size: cover;
        background-repeat: no-repeat;
        padding: 33px 30px 33px 22px;
        border-radius: 15px;

        @include tablet {
            padding: 22px 21px 20px 20px;
        }

        @include mobile-final {
            padding: 16px;
        }
    }

    &__title {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 48px;
        color: var(--tekst-zagolovok);
        line-height: 1.3;
        letter-spacing: -1.5px;
        margin-bottom: 35px;

        @include tablet {
            font-size: 32px;
            width: 100%;
            max-width: 332px;
        }

        @include mobile-final {
            font-size: 20px;
            margin-bottom: 14px;
        }
    }
}

.form {

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 26px;

        @include tablet {
            flex-direction: column;
            align-items: end;
            margin-top: -242px;
            gap: 10px;
            margin-bottom: 10px;
        }

        @include mobile {
            width: 100%;
        }

        @include pre-mobile {
            margin-top: 0;
            align-items: flex-start;
            width: 80%;
            gap: 6px;
            width: 100%;
            margin-bottom: 8px;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        gap: 15px;

        @include tablet {
            gap: 5px;
        }

        @include pre-mobile {
            width: 100%;
            gap: 0px;
        }
    }

    &__label {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 30px;
        color: var(--tekst-zagolovok);

        @include tablet {
            font-size: 18px;
            letter-spacing: -.6px;
        }

        @include mobile-final {
            font-size: 14px;
        }
    }

    &__input {
        border: 1px solid rgba(255, 255, 255, 0.5);
        border-radius: 10px;
        width: 330px;
        height: 59px;
        background-color: transparent;
        padding: 20px 16px;
        transition: all .3s ease-in-out;

        @include for-desktop {
            width: 266px;
        }

        @include tablet {
            width: 312px;
            height: 42px;
        }

        @include pre-mobile {
            width: 100%;
        }

        @include mobile-final {
            height: auto;
            padding: 6px 10px;
        }
        @include hover {
            border: 1px solid var(--color---white);
        }

        &::placeholder {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 18px;
            line-height: 150%;
            letter-spacing: 0.05em;
            color: var(--tekst-zagolovok);
            @include small-tablet {
                font-size: 14px;
            }

            @include mobile-final {
                font-size: 14px;
                line-height: 125%;
                letter-spacing: -0.9px;
            }
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 78px;

        @include tablet {
            flex-direction: column;
            align-items: end;
            gap: 0px;
        }

        @include pre-mobile {
            align-items: flex-start;
        }
    }

    &__textAreaBox {
        width: 100%;
        max-width: 738px;
        height: 163px;

        @include tablet {
            max-width: 312px;
            height: auto;
        }

        @include pre-mobile {
            width: 100%;
            max-width: 100%;
        }

        @include mobile {
            width: 100%;
        }
        
    }

    &__textarea {
        border: 1px solid rgba(255, 255, 255, 0.5);
        border-radius: 10px;
        width: 100%;
        background-color: transparent;
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.05em;
        color: var(--tekst-zagolovok);
        padding: 20px 16px;
        transition: all .3s ease-in-out;

        @include hover {
            border: 1px solid var(--color---white);
        }

        @include tablet {
            padding: 10px 16px;
        }

        @include mobile-final {
            font-size: 14px;
            line-height: 125%;
            letter-spacing: -0.9px;
            height: 97px;
        }
    }

    &__button {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        font-family: inherit;
        font-weight: 500;
        font-size: 30px;
        line-height: 150%;
        color: var(--tekst-zagolovok);
        background-color: transparent;
        border: 1px solid var(--tekst-zagolovok);
        width: 492px;
        height: 76px;
        border-radius: 10px;
        padding: 13px 85px;
        width: 330px;
        height: 72px;
        cursor: pointer;
        pointer-events: auto;
        transition: background-color .3s ease-in-out;

        @include tablet {
            width: 312px;
            font-size: 20px;
            height: 59px;
            margin-top: 34px;
        }

        @include mobile {
            width: 100%;
        }

        @include mobile-final {
            font-size: 16px;
            padding: 10px 65px;
            height: auto;
            width: auto;
            margin-top: 0px;
        }

        &:focus {
            outline: 1px solid var(--color---white);
        }

        @include hover {
            background-color: var(--tekst-zagolovok);
            color: var(--osnovnoy-emerald);
        }
    }

    &__checkbox {
        border: 1px solid var(--tekst-zagolovok);
        border-radius: 2px;
        width: 12px;
        height: 12px;
    }

    &__policy {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 125%;
        letter-spacing: 0.03em;
        color: var(--tekst-zagolovok);
        transition: color .3s ease-in;
        padding-left: 20px;

        @include tablet {
            letter-spacing: 0px;
        }
    }

    &__policy-link {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 125%;
        letter-spacing: 0.03em;
        color: var(--tekst-zagolovok);
        transition: color .3s ease-in;
        text-decoration: underline;

        &:focus {
            outline: 1px solid var(--color---white);
        }

        @include tablet {
            letter-spacing: 0px;
        }

        @include hover {
            color: rgba(255, 255, 255, 0.5);
        }
        
    }

    &__consent {
        position: relative;
        
        @include pre-mobile {
            align-items: flex-start;
        }
    }

    &__end {
        align-self: flex-end;
        gap: 0;
        justify-content: flex-start;
        width: 330px;

        @include tablet {
            width: 312px;
            flex-direction: column-reverse;
            gap: 5px;
        }

        @include pre-mobile {
            width: 100%;
            align-items: flex-start;
            margin-top: 5px;
        }
    }
}

.select {
    position: relative;
    width: 330px;
    height: 59px;

    @include tablet {
        width: 312px;
        height: auto;
    }

    @include pre-mobile {
        width: 100%;
    }

    &__name {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.05em;

        @include small-tablet {
            font-size: 14px;
        }
    }

    &__selected {
        border-radius: 10px;
        background-color: transparent;
        padding: 16px 16px;
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.05em;
        color: var(--tekst-zagolovok);
        border: 1px solid rgba(255, 255, 255, 0.5);
        display: flex;
        justify-content: space-between;
        align-items: center;
        transition: border-color .3s ease-in-out;
        cursor: pointer;

        @include tablet {
            padding: 7px 16px;
        }

        @include mobile-final {
            font-size: 14px;
            padding: 3px 6px;
            letter-spacing: 0px;
        }

        @include hover {
            border-color: var(--color---white);
        }

        &:focus-visible {
            outline: none !important;
            box-shadow: none !important;
            box-shadow: 0 0 0 2px var(--color---white); 
        }
    }

    &__options {
        display: flex;
        flex-direction: column;
        gap: 10px;
        position: absolute;
        top: calc(100% + 6px); 
        left: 0;
        width: 100%;
        background: #fff;
        color: #555;
        border-radius: 14px;
        overflow: hidden;
        z-index: 100;
        padding: 16px;
        overflow: hidden;
        opacity: 0;
        transform: translateY(-5px);
        transition: opacity 0.25s ease, transform 0.25s ease;
        pointer-events: none;
        @include small-tablet {
            padding: 10px 16px;
        }

        &:focus-visible {
            outline: none !important;
            box-shadow: none !important;
            box-shadow: 0 0 0 2px var(--color---white); 
        }
    }

    &__option {
        font-weight: 400;
        font-size: 18px;
        line-height: 1.5;
        letter-spacing: 0.05em;
        color: #7d7d7d;
        padding: 10px 16px;
        border-radius: 5px;
        cursor: pointer;

        @include small-tablet {
            padding: 5px;
            font-size: 14px;
        }

        &:focus-visible {
            outline: none;
            box-shadow: none;
            box-shadow: 0 0 0 2px var(--color---white); 
        }
    }

    &__arrowBox {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        height: 24px;

        @include mobile-final {
            margin-right: 5px;
        }
    }

    &__arrow {
        transition: transform 0.25s ease;
    }
}
.select.open .select__options {
  opacity: 1;
  transform: translateY(0);
  pointer-events: auto;
  pointer-events: all;
}

.row-close {
    display: block;
    margin-top: -4px;
    margin-left: 19px;
    
    @include mobile-final {
        display: inline;
        margin-top: 0;
        margin-left: 0;
    }
}