.header {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 1;
    margin-top: 40px;
    background-color: transparent;
    transition: all .5s ease-in;
    @include tablet {
       padding-top: 17px; 
       margin-top: 0px;
    }

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 38px;
        @include for-desktop {
            height: auto;
        }
    }
    
    &__logo {
        font-family: var(--second-family);
        font-weight: 700;
        font-size: 32px;
        letter-spacing: -0.05em;
        color: var(--osnovnoy-hover);
        @include for-desktop {
            font-size: 24px;
        }
        @include tablet {
            font-size: 16px;
        }
    }
}
.header.fixed {
    position: absolute;
    z-index: 100;
    padding: 10px 0;
    background-color: rgba(0, 0, 0, 0.7);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(4px);
}

