
.question {
    padding-bottom: 80px;

    @include small-tablet {
        padding-bottom: 40px;
    }

    &__inner {
      display: flex;
      justify-content: space-between;

      @include small-tablet {
        flex-direction: column;
        gap: 32px;
      }
    }

    &__title {
      @include for-desktop {
        font-size: 38px;
	    }

      @include small-tablet {
        width: 100%;
        max-width: 380px;
        font-size: 32px;
      }

      @include mobile-final {
        font-size: 20px;
        letter-spacing: -0.9px;
      }
    }
}

.accordion {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 659px;
  gap: 16px;

  @include tablet {
    max-width: 100%;  
	}

  &__item {
    border-radius: 15px;
    padding: 32px;
    padding-top: 54px;
    height: 100%;
    background: var(--color---green-12);

    @include tablet {
      padding: 20px; 
      padding-top: 44px;
	  }

    @include mobile-final {
      padding: 16px;
      padding-top: 38px;
	  }

    &.active .accordion__btn {
      margin-bottom: 32px;

      @include mobile-final {
        margin-bottom: 12px;
      }
    }

    &.active .accordion__btn svg{
      transform: rotate(45deg);
    }
  }

  &__question {
    position: relative;
  }

  &__answer {
    overflow: hidden;
    max-height: 0;
    text-align: start;
    transition: all 0.4s ease;

    @include mobile-final {
      font-size: 14px;
      line-height: 125%;
      letter-spacing: 0.5px;
    }
  }

  &__count {
    position: absolute;
    left: 0;
    top: -25px;
    font-weight: 400;
    font-size: 14px;
    line-height: 125%;
    letter-spacing: 0.03em;
    color: var(--color---green-100);
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    font-weight: 700;
    font-size: 24px;
    text-align: left;
    color: var(--tekst-zagolovok);
    transition: all .3s ease-in-out;

    @include for-desktop {
        font-size: 20px;
        gap: 10px;
	  }

    @include mobile-final {
      align-items: baseline;
      font-size: 18px;
    }
  }

  &__btn-svg {
    flex-shrink: 0;
    width: 20px;
    height: 20px;
  }
}

.accordion__btn svg {
  transition: transform .3s ease-in-out;
}

.color-one {
  background: var(--color---green-12);
}

.color-two {
  background: var(--color---cyan-12);
}

.color-three {
  background: var(--color---dark-blue-12);
}

.question-four {
  background: var(--color---light-blue-12);
}

.question-five {
  background: var(--color---green-12);
}


