.steps {
    padding-bottom: 120px;

    @include tablet {
        padding-bottom: 80px;
    }

    @include mobile-final {
        padding-bottom: 64px;
    }

    &__title {
        margin-bottom: 32px;
        width: 100%;
        max-width: 618px;

        @include tablet {
            max-width: 370px;
            font-size: 32px;
            letter-spacing: -1.2px;
        }

        @include mobile-final {
            margin-bottom: 23px;
            font-size: 20px;
            letter-spacing: -0.6px;
        }
    }
}

.steps-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 32px;

    @include for-desktop {
       flex-wrap: wrap;
       justify-content: center;
    }

    @include small-tablet {
		align-items: flex-start;
        justify-content: left;
	}

    @include mobile-final {
        gap: 20px;
    }
}

.step {
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    padding: 32px 32px 59px ;
    width: 276px;
    height: 434px;
    background-color: var(--color---green-12);
    transition: transform .3s ease-in;
    cursor: pointer;

    @include hover {
        transform: translateY(-20px);
    }

    @include for-desktop {
        width: 336px;
        height: 325px;
    }

    @include tablet {
        padding: 20px;
        width: 336px;
        height: 155px;
    }

    @include small-tablet {
        width: 100%;
		max-width: 500px;
	}

    @include pre-mobile {
		height: 130px;
	}

    @include mobile-final {
        padding: 16px 15px;
        width: auto;
        height: auto;
        max-width: 100%;
    }

    &__title {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 36px;
        line-height: 1.3;
        letter-spacing: -1.8px;
        color: var(--tekst-zagolovok);

        @include for-desktop {
            font-size: 28px;
        }

        @include tablet {
            font-size: 24px;
            letter-spacing: -0.6px;
        }

        @include mobile-final {
            margin-bottom: 25px;
            font-size: 18px;
        }
    }

    &__num {
        margin-bottom: 110px;
        font-weight: 400;
        font-size: 14px;
        line-height: 125%;
        letter-spacing: 0.03em;
        color: var(--color---light-blue-100);

        @include for-desktop {
            margin-bottom: 60px;
        }

        @include tablet {
            margin-bottom: 0;
        }
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
    }

    &__description {
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.05em;
        color: var(--tekst-abzac);

        @include for-desktop {
            font-size: 15px;
        }

        @include mobile-final {
            font-size: 16px;
        }
    }
}

.step--colorOne {
    background-color : var(--color---green-12);

    @include mobile-final {
        width: 100%;
    }

    .step__num {
        color: var(--color---green-100);
    }
}

.step--colorTwo {
    background-color: var(--color---cyan-12);

    @include mobile-final {
        width: 100%;
    }

    .step__num {
        color: var(--color---cyan-100);
    }
}

.step--colorThree {
    padding-bottom: 32px;
    background-color: var(--color---dark-blue-12);

    @include tablet {
        padding-bottom: 20px;
    }

    @include mobile-final {
        padding: 16px 15px;
        width: 100%;
    }

    .step__num {
        color: var(--color---dark-blue-100);
    }
}

.step--colorFour {
    background-color: var(--color---light-blue-12);

    @include mobile-final {
        width: 100%;
    }
    
    .step__num {
        color: var(--color---light-blue-100);
    }
}