.btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-family: inherit;
    font-weight: 500;
    font-size: 30px;
    line-height: 150%;
    width: 492px;
    height: 76px;
    color: var(--tekst-zagolovok);
    background: transparent;
    border: none;
    padding: 16px 105px 15px 80px;
    border-radius: 10px;
    position: relative;
    z-index: 0;
    overflow: hidden;
    cursor: pointer;
    @include for-desktop-large {
        width: 337px;
        padding-right: 69px;
        padding-left: 54px;
    }
    @include tablet {
        width: 295px;
        height: 57px;
        letter-spacing: -.9px;
        padding-right: 35px;
        display: flex;
        justify-content: space-between;
    }
    @include mobile {
      margin-top: auto;
    }
    @include mobile-final {
        width: 100%;
    }
    @include mobile-final {
      height: 44px;
      padding: 10px 23px;
      justify-content: space-between;
      font-size: 16px;
      padding-right: 40px;
    }

    &__arrow {
        display: inline-block;
        position: relative;
        z-index: 2;
        margin-left: 111px;
        width: 24px;
        height: 24px;
        transform: translateX(-5px);
        transition: transform .3s ease-in;
        @include for-desktop-large {
            margin-left: 77px;
        }
    }

    &__text {
        position: relative;
        z-index: 2;
        font-weight: 500;
        font-size: 20px;
        line-height: 150%;
        color: var(--tekst-zagolovok);
        @include mobile-final {
            font-size: 16px;
            letter-spacing: -0.5px;
        }
    }

    &__effect {
        position: absolute;
        left: 0;
        top: 0;
        background-color: var(--osnovnoy-hover);
        width: 27%;
        height: 100%;
        border-radius: 10px;
        z-index: 1;
        transition: width 0.4s ease;
        @include small-tablet  {
            width: 100%;
        }
    }
    @include hover {
        .btn__effect {
            width: 100%; 
        }
    }
    @include hover {
        .btn__arrow {
            transform: translateX(0);
        }
    }

}