.teachers {
	padding-bottom: 120px;

	@include tablet {
        padding-bottom: 80px;
    }

	@include mobile-final {
        padding-bottom: 64px;
    }

	&__title {
		margin-bottom: 32px;
		width: 100%;
		max-width: 650px;

		@include tablet {
			max-width: 450px;
    		font-size: 32px;
			letter-spacing: -1px;
  		}

		@include mobile-final {
			margin-bottom: 23px;
            font-size: 20px;
        }

	}

	&__inner {
		display: flex;
		flex-direction: column;
		gap: 34px;

		@include for-desktop-large {
			flex-direction: row;
			justify-content: space-between;
		}

		@include small-tablet {
			flex-direction: column;
			gap: 16px;
		}
	}
}

.teacher-card {
	display: flex;
	justify-content: space-between;
	gap: 32px;

	@include for-desktop-large {
		flex-direction: column;
	}

	@include small-tablet {
		margin-right: auto;
		width: 100%;
		max-width: 570px;
		gap: 10px;
	}

	@include mobile-final {
		gap: 1px;
	}

	&:last-child {
		flex-direction: row-reverse;

		@include for-desktop-large {
			flex-direction: column;
		}
	}

	&__description {
		width: 584px;

		@include for-desktop-large {
			order: 2;
			width: 100%;
		}
		
	}

	&__name {
		margin-bottom: 32px;
		border-radius: 15px;
		padding: 32px;
		font-weight: 700;
		font-size: 36px;
		line-height: 1.32;
		letter-spacing: -1.9px;
		color: var(--osnovnoy-hover);
		background: var(--color---light-blue-12);

		@include tablet {
			padding: 20px;
			font-size: 24px;
    	}

		@include small-tablet {
			margin-bottom: 18px;
			padding: 15px;
			font-size: 22px;
		}

		@include mobile-final {
			margin-bottom: 6px;
			padding: 18px 20px;
			font-size: 18px;
			letter-spacing: -0.9px;
        }
	}

	&__wrapper {
		display: flex;
		justify-content: space-between;
		border-radius: 15px;
		padding: 32px;
		height: 433px;
		background: var(--color---light-blue-12);
		gap: 16px;

		@include for-desktop-large {
			flex-direction: column;
			justify-content: flex-start;
			padding: 20px;
			height: auto;
			gap: 16px;
		}

		@include tablet {
			height: 500px;
    	}

		@include small-tablet {
			height: auto;
		}

		@include mobile-final {
			padding: 16px;
		}
	}

	&__experience {
		display: flex;
		flex-direction: column;
		gap: 16px;

		@include mobile-final {
			gap: 10px;
		}

		&:first-child {
			width: 274px;

			@include mobile-final {
				width: 100%;
				max-width: 274px;
			}
		}

		&:last-child {
			width: 288px;

			@include mobile-final {
				width: 100%;
				max-width: 274px;
			}
		}
	}

	&__experience-title {
		margin-bottom: 2px;
		font-weight: 700;
		font-size: 30px;
		line-height: 1.32;
		letter-spacing: -1.2px;
		color: var(--tekst-zagolovok);

		@include tablet {
			font-size: 18px;
    	}

		@include mobile-final {
			font-weight: 600;
			font-size: 16px;
			letter-spacing: -0.2px;
		}
	}

	&__experience-desc {
		font-weight: 400;
		font-size: 18px;
		line-height: 150%;
		letter-spacing: 0.05em;
		color: var(--tekst-abzac);

		@include tablet {
			font-size: 16px;
    	}
	}

	&__photo {
		width: 100%;

		@include for-desktop-large {
			order: 1;
		}
	}

	&__img {
		border-radius: 15px;
		width: 100%;
	}
}
