.popup {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 0;
    overflow: hidden auto;
    visibility: hidden;
    transition: all .3s ease-in;
    z-index: 1000;
    &__wrapper {
        min-height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__content {
        width: 100%;
        max-width: 626px;
        padding: 32px;
        height: 292px;
        background: var(--fon-charcoal);
        display: flex;
        border-radius: 15px;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;

        @include small-tablet {
            height: auto;
            gap: 20px;
        }
    }
    &__title {
        font-weight: 700;
        font-size: 30px;
        text-align: center;
        color: var(--color---white);
        letter-spacing: -1px;

        @include small-tablet {
            font-size: 18px;
            letter-spacing: 1px;
        }
    }
    &__btn {
        cursor: pointer;
        background: var(--fon-charcoal);
        border: 1px solid var(--tekst-zagolovok);
        border-radius: 10px;
        padding: 14px 102px;
        width: 312px;
        height: 58px;
        font-size: 30px;

        @include small-tablet {
            font-size: 20px;
        }
    }
}
.popup.open {
    visibility: visible;
    opacity: 1;
}