.intro{
  width: 100%;
  height: 740px;
  z-index: 0;
  background-image: url('/img/intro-bg.png');
  background-repeat: no-repeat;
  background-size: cover;

  @include for-desktop-large {
    height: 680px;
  }

  @include tablet {
    height: 530px;
    background-position: center;
  }

  @include mobile {
    height: auto;
    padding-bottom: 32px;
    background-image: url('../../img/intro-bg-mobile.png');
  }

  @include mobile-final {
    height: auto;
  }

  &__content {
    position: relative;
    padding-top: 158px;
    width: 100%;
    display: flex;
    flex-direction: column;

    @include tablet {
      padding-top: 82px;
    }

    @include mobile {
      height: 815px;
      padding-top: 90px;
    }

    @include mobile-final {
      height: 690px;
    }

    @include mobile-final {
      height: auto;
      padding-top: 64px;
    }
  }

  &__title {
    display: inline-block;
    margin: 0;
    width: 829px;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 48px;
    letter-spacing: -1.8px;
    color: var(--tekst-zagolovok);
    line-height: 1.24;

    @include for-desktop-large {
      max-width: 650px;
      width: 100%;
      font-size: 36px;
    }

    @include tablet {
      font-size: 32px;
      max-width: 566px;
      line-height: 1.28;
      letter-spacing: -1.5px;
    }

    @include mobile-final {
      font-size: 20px;
      letter-spacing: -0.6px;
    }
  }
  &__text {
    display: inline-block;
    width: 486px;
    margin: 48px 0 100px;
    font-weight: 400;
    font-size: 28px;
    line-height: 165%;
    letter-spacing: 0.05em;
    color: var(--tekst-abzac);

    @include for-desktop-large {
      max-width: 485px;
      width: 100%;
      font-size: 24px;
    }

    @include tablet {
      font-size: 22px;
      margin-top: 21px;
      margin-bottom: 95px;
      letter-spacing: .2px;
      max-width: 440px;
    }

    @include pre-mobile {
      font-size: 20px;
      max-width: 340px;
    }

    @include mobile-final {
      font-size: 16px;
      max-width: 100%;
      margin-bottom: 0px;
      margin-top: 12px;
      letter-spacing: 0.1px;
      line-height: 1.5;
    }
  }

  &__img {
    position: absolute;
    bottom: -6px;
    right: -20px;
    width: 515px;
    height: auto;

    @include for-desktop-large {
      right: -8px;
      width: 460px;
      height: 460px;
    }

    @include tablet {
      width: 321px;
      height: 323px;
      bottom: -6px;
      right: -13px;
    }

    @include pre-mobile {
      width: 280px;
      height: auto;
      right: 0;
    }

    @include mobile {
      width: 370px;
      left: 0;
      bottom: 75px;
    }
    
    @include mobile-final {
      position: static;
      top: auto;
      left: auto;
      bottom: auto;
      right: auto;
      width: 100%;
      margin-bottom: 16px;
    }
  }
}
.text-yel {
    font-family: var(--second-family);
    color: #f2e186;
}

.text-orang {
    letter-spacing: -0.2px;
    font-family: var(--second-family);
    color: #df7e29;
}